import TableContextContainer from '../../components/tableContextContainer';
import { ContextContainer } from './contextContainer';

const { getTableIsSelected, visibleUtil } = utils;

function getColIndex(cell: HTMLTableCellElement, border: 'left' | 'right', table: MoTable) {
  const cellKey = cell.getAttribute('data-cangjie-key')!;
  const position = getPositionOfCell(
    cellKey,
    table,
  );

  const colIndex = position && position.colIndex;
  if (!(typeof colIndex === 'number' && colIndex >= 0)) {
    return null;
  }

  return border === 'left'
    ? colIndex - 1
    : colIndex + (parseInt(cell.getAttribute('colspan')!, 10) || 1) - 1;
}

function getRowKey(cell: HTMLTableCellElement, border: 'top' | 'bottom' | false) {
  const row = cell.parentElement;
  if (!row) {
    return null;
  }
  const rowElements = Array.from(row.parentElement!.children);
  const rowIndex = rowElements.indexOf(row!);
  const adjustedRowIndex = border === 'top' ? rowIndex - 1 : rowIndex;
  const adjustedRow = rowElements[adjustedRowIndex];
  if (adjustedRow) {
    return adjustedRow.getAttribute('data-cangjie-key');
  }
  return null;
}

function getPrevVisibleCell(cell: HTMLTableCellElement) {
  let prevVisibleCell = cell.previousElementSibling as HTMLTableCellElement;
  while (prevVisibleCell && prevVisibleCell.style.display === 'none') {
    prevVisibleCell = prevVisibleCell.previousElementSibling as HTMLTableCellElement;
  }
  return prevVisibleCell;
}

function getNextVisibleCell(cell: HTMLTableCellElement) {
  let nextVisibleCell = cell.nextElementSibling as HTMLTableCellElement;
  while (nextVisibleCell && nextVisibleCell.style.display === 'none') {
    nextVisibleCell = nextVisibleCell.nextElementSibling as HTMLTableCellElement;
  }
  return nextVisibleCell;
}

export interface Selection {
  startRowIndex: number;
  startColIndex: number;
  endRowIndex: number;
  endColIndex: number;
}

let lastActiveTableKey: string | null = null;
const { closeContextMenu } = actions;

function getLastActiveTableKey() {
  return lastActiveTableKey;
}

export interface TableProps {
  controller: Controller;
  isSelected: boolean;
  node: MoTable;
  colsWidth: number[];
  scale: number;
  zoomContainer: HTMLElement;
  scrollableContainer?: Window | HTMLElement | null;
  tableSelection?: Omit<ITableSelection, 'key'> | null;
  children: () => React.ReactNode;
  tableConfig?: TableConfig;
  draggableConfig?: BiPluginConfig['draggable'];
  locale: any;
  visible?: boolean;
}

interface TableState {
  shouldShowTableLeftShadow: boolean;
  shouldShowTableRightShadow: boolean;
  isSelectionHighlight: boolean;
  hoverSelection: ITableSelection | null;
  isHoverTable: boolean;
  isHoverCornerToolbar: boolean;
}

class Table extends React.Component<TableProps, TableState> {
  colResizeable: false | 'left' | 'right' = false;

  rowResizeable: false | 'top' | 'bottom' = false;

  resizingColIndex: number | null = null;

  resizingRowKey: string | null = null;

  resizingColCell: HTMLTableCellElement | null = null;

  resizingColNextCell: HTMLTableCellElement | null = null;

  resizingRow: HTMLTableCellElement | null = null;

  startTableX: number | null = null;

  startTableY: number | null = null;

  wrapperRef: React.RefObject<HTMLDivElement> = React.createRef();

  realTableWrapperRef: React.RefObject<HTMLDivElement> = React.createRef();

  tableRef: React.RefObject<HTMLTableElement> = React.createRef();

  colResizerRef: React.RefObject<HTMLDivElement> = React.createRef();

  rowResizerRef: React.RefObject<HTMLDivElement> = React.createRef();

  colIndicatorRef: React.RefObject<HTMLTableElement> = React.createRef();

  rowIndicatorRef: React.RefObject<HTMLTableElement> = React.createRef();

  tableResizeControllerRef: React.RefObject<HTMLDivElement> = React.createRef();

  tableResizeControlLinesRef: React.RefObject<
  HTMLDivElement
  > = React.createRef();

  tableLeftShadowRef: React.RefObject<HTMLDivElement> = React.createRef();

  tableRightShadowRef: React.RefObject<HTMLDivElement> = React.createRef();

  lastEndCellKey: string | null = null;

  tableSelection: ITableSelection | null = null;

  colsNodes: HTMLDivElement[] = [];

  updateShadowRafId: number | null = null;

  tableCursorStyle: string | null = null;

  isResizingTable = false;

  constructor(props: TableProps) {
    super(props);
    this.state = {
      shouldShowTableLeftShadow: false,
      shouldShowTableRightShadow: false,
      isSelectionHighlight: false,
      hoverSelection: null,
      isHoverTable: false,
      isHoverCornerToolbar: false,
    };
  }

  componentDidMount() {
    this.updateOnRender();
    this.updateShadowState();
    this.updateScrollLeft();
  }

  componentDidUpdate(prevProps: TableProps) {
    fastdom.measure(() => {
      const { node, isSelected } = this.props;
      this.updateOnRender();

      const { node: prevNode, isSelected: prevIsSelected } = prevProps;
      if (
        isSelected !== prevIsSelected ||
        (node.data.colsWidth || []).length !==
        (prevNode.data.colsWidth || []).length
      ) {
        this.handleTableScroll();
      }
      if (node.data?.percentOfScrollLeft !== prevNode.data?.percentOfScrollLeft) {
        this.updateScrollLeft();
      }
    });
  }

  componentWillUnmount() {
    this.handleTableScroll.cancel();
    this.updateOnRender.cancel();
    if (this.updateShadowRafId !== null) {
      window.cancelAnimationFrame(this.updateShadowRafId);
    }

    document.removeEventListener('mousemove', this.handleColResizing);
    document.removeEventListener('mouseup', this.handleColResizeEnd);

    document.removeEventListener('mousemove', this.handleRowResizing);
    document.removeEventListener('mouseup', this.handleRowResizeEnd);

    document.removeEventListener('mousemove', this.handleTableResizing);
    document.removeEventListener('mouseup', this.handleTableResizeEnd);
  }

  isInNestedTable(event) {
    const table = event.target.closest('table');
    return table !== ReactDOM.findDOMNode(this.tableRef.current);
  }

  isSelected() {
    const { isSelected } = this.props;
    return isSelected || Boolean(this.getTableSelection());
  }

  closeContextMenu = () => {
    const { controller } = this.props;
    controller.run('onAction', closeContextMenu());
  };

  getContainerWidth = () => {
    const { controller, node } = this.props;
    const { value } = controller;
    const containerBlock = getClosestContainerBlock(value, node);
    const containerWidth = getInnerWidth(containerBlock);
    return containerWidth;
  };

  handleMouseEnter = () => {
    if (!this.realTableWrapperRef.current) return;

    this.tableCursorStyle = this.realTableWrapperRef.current.style.cursor;
  };

  handleMouseMove = (event) => {
    const { zoomContainer } = this.props;
    const relativeEvent = getRelativeMouseEvent(event, zoomContainer);
    if (
      this.startTableX || // 
      this.startTableY ||
      this.isInNestedTable(event)
    ) {
      return;
    }

    const targetCell = event.target.closest('td');
    const prevCursor = this.tableCursorStyle;

    if (!targetCell) {
      return;
    }

    const targetCellRect = getBoundingRelativeRect(targetCell, zoomContainer);
    const deltaToLeft = relativeEvent.relativeX - targetCellRect.left;
    const deltaToRight = targetCellRect.right - relativeEvent.relativeX;
    const deltaToTop = relativeEvent.relativeY - targetCellRect.top;
    const deltaToBottom = targetCellRect.bottom - relativeEvent.relativeY;

    // DETECT_DELTAscale

    if (deltaToLeft <= DETECT_DELTA) {
      if (!targetCell.previousElementSibling) {
        this.colResizeable = false;
        targetCell.style.cursor = prevCursor;
      } else {
        this.colResizeable = 'left';
        this.rowResizeable = false;
        targetCell.style.cursor = 'col-resize';
      }
    } else if (deltaToRight <= DETECT_DELTA) {
      this.colResizeable = 'right';
      this.rowResizeable = false;
      targetCell.style.cursor = 'col-resize';
    } else if (deltaToTop <= 4) {
      if (!targetCell.parentElement.previousElementSibling) {
        this.rowResizeable = false;
        targetCell.style.cursor = prevCursor;
      } else {
        this.rowResizeable = 'top';
        this.colResizeable = false;
        targetCell.style.cursor = 'row-resize';
      }
    } else if (deltaToBottom <= DETECT_DELTA) {
      this.rowResizeable = 'bottom';
      this.colResizeable = false;
      targetCell.style.cursor = 'row-resize';
    } else {
      this.rowResizeable = false;
      this.colResizeable = false;
      // 
      targetCell.style.cursor = prevCursor;
    }
  };

  handleMouseDown = (event: React.MouseEvent<HTMLTableElement>) => {
    const { node } = this.props;
    lastActiveTableKey = node.key;
    //  mouseDown 
    if (
      this.isInNestedTable(event) ||
      event.buttons === 2
    ) {
      return;
    }

    if (this.colResizeable) {
      this.handleColResizeStart(event);
    } else if (this.rowResizeable) {
      this.handleRowResizeStart(event);
    }
  };

  handleTableResizeStart = (event) => {
    const { scale, zoomContainer } = this.props;
    event.preventDefault();
    event.stopPropagation();
    const relativeEvent = getRelativeMouseEvent(event, zoomContainer);
    const realTableWrapper = ReactDOM.findDOMNode(this.realTableWrapperRef.current) as HTMLElement;
    const realTableWrapperRect = getBoundingRelativeRect(
      realTableWrapper,
      zoomContainer,
    );

    this.startTableX = Math.round(
      (relativeEvent.relativeX - realTableWrapperRect.left) / scale,
    );
    this.startTableY = Math.round(
      (relativeEvent.relativeY - realTableWrapperRect.top) / scale,
    );
    if (this.tableResizeControlLinesRef.current) {
      this.tableResizeControlLinesRef.current.style.display = 'block';
    }

    this.isResizingTable = true;

    document.addEventListener('mousemove', this.handleTableResizing);
    document.addEventListener('mouseup', this.handleTableResizeEnd);
  };

  handleTableResizing = (event) => {
    const { scale, zoomContainer } = this.props;
    if (!zoomContainer) return;

    event.preventDefault();
    event.stopPropagation();
    const relativeEvent = getRelativeMouseEvent(event, zoomContainer);
    const realTableWrapper = this.realTableWrapperRef.current;
    if (!realTableWrapper || !this.tableRef.current || !this.tableResizeControllerRef.current) return;
    const realTableWrapperRect = getBoundingRelativeRect(
      realTableWrapper,
      zoomContainer,
    );
    const table = this.tableRef.current;
    const tableRect = getBoundingRelativeRect(table, zoomContainer);
    const tableResizeController = this.tableResizeControllerRef.current;

    const currentTableX = Math.max(
      MIN_COL_WIDTH,
      Math.round((relativeEvent.relativeX - realTableWrapperRect.left) / scale),
    );
    const currentTableY = Math.max(
      MIN_ROW_HEIGHT,
      Math.round((relativeEvent.relativeY - tableRect.top) / scale),
    );

    tableResizeController.style.left = `${currentTableX}px`;
    tableResizeController.style.top =
      `${currentTableY +
      Math.round((tableRect.top - realTableWrapperRect.top) / scale)
      }px`;
  };

  handleTableResizeEnd = (event) => {
    this.isResizingTable = false;
    event.preventDefault();
    event.stopPropagation();
    const { controller, node, zoomContainer, scale } = this.props;
    const rowsHeight = controller.query('getAllRowsHeight', {
      node,
    });

    const relativeEvent = getRelativeMouseEvent(event, zoomContainer);
    const realTableWrapper = this.realTableWrapperRef.current;
    if (!realTableWrapper) {
      return;
    }
    const realTableWrapperRect = getBoundingRelativeRect(
      realTableWrapper,
      zoomContainer,
    );

    const deltaX =
      Math.round((relativeEvent.relativeX - realTableWrapperRect.left) / scale) -
      this.startTableX!;
    const deltaY =
      Math.round((relativeEvent.relativeY - realTableWrapperRect.top) / scale) -
      this.startTableY!;

    this.startTableX = null;
    this.startTableY = null;
    if (this.tableResizeControlLinesRef.current) {
      this.tableResizeControlLinesRef.current.style.display = 'none';
    }
    document.removeEventListener('mousemove', this.handleTableResizing);
    document.removeEventListener('mouseup', this.handleTableResizeEnd);
    const maxTableWidth = controller.query('maxTableWidth', { node });
    controller.run('onAction', resizeTableWidth(node, deltaX, maxTableWidth));
    controller.run('onAction', resizeTableHeight(node, rowsHeight, deltaY));
    this.updateShadowState();
  };

  handleColResizeStart = (event) => {
    const { scale, zoomContainer, node } = this.props;
    const relativeEvent = getRelativeMouseEvent(event, zoomContainer);

    event.preventDefault();

    const targetCell = event.target.closest('td');
    if (!targetCell) {
      return;
    }
    // @ts-ignore
    this.resizingColIndex = getColIndex(targetCell, this.colResizeable, node);
    if (typeof this.resizingColIndex !== 'number') {
      return;
    }
    this.resizingColCell =
      this.colResizeable === 'left'
        ? getPrevVisibleCell(targetCell)
        : targetCell;
    this.resizingColNextCell =
      this.colResizeable === 'left'
        ? targetCell
        : getNextVisibleCell(targetCell);

    const realTableWrapper = ReactDOM.findDOMNode(this.realTableWrapperRef.current) as HTMLElement;
    const realTableWrapperRect = getBoundingRelativeRect(
      realTableWrapper,
      zoomContainer,
    );
    const table = ReactDOM.findDOMNode(this.tableRef.current) as HTMLElement;
    const tableRect = getBoundingRelativeRect(table, zoomContainer);
    const firstRowDOM = table.querySelector('tr');
    if (!firstRowDOM) return;

    const firstRowRect = getBoundingRelativeRect(firstRowDOM, zoomContainer);

    this.startTableX = relativeEvent.relativeX - realTableWrapperRect.left;

    const colResizer = ReactDOM.findDOMNode(
      this.colResizerRef.current,
    ) as HTMLElement;
    colResizer.style.display = 'block';
    colResizer.style.height = `${
      Math.round(tableRect.bottom - firstRowRect.top) / scale +
      (this.isSelected() ? TOOLBAR_ITEM_SIZE / scale : 0)
    }px`;

    colResizer.style.left = `${Math.round(this.startTableX / scale)}px`;

    document.addEventListener('mousemove', this.handleColResizing);
    document.addEventListener('mouseup', this.handleColResizeEnd);
  };

  handleColResizing = (event) => {
    const { node, colsWidth } = this.props;
    const { scale, zoomContainer } = this.props;
    if (!zoomContainer) return;

    const relativeEvent = getRelativeMouseEvent(event, zoomContainer);

    event.preventDefault();

    const realTableWrapper = this.realTableWrapperRef.current;
    const colResizer = this.colResizerRef.current;

    // Fix: 
    if (!realTableWrapper || !colResizer || !this.resizingColCell) return;

    const realTableWrapperRect = getBoundingRelativeRect(
      realTableWrapper,
      zoomContainer,
    );
    const resizingColCellRect = getBoundingRelativeRect(
      this.resizingColCell,
      zoomContainer,
    );
    const resizingColCellRectLeft = resizingColCellRect.left;
    const resizingColCellIndex =
      this.resizingColIndex! -
      parseInt(this.resizingColCell.getAttribute('colspan')!, 10) +
      1;
    if (!node.data.colsWidth) {
      logNPEInfo(node, {
        type: `node.data.colsWidth is ${node.data.colsWidth}`,
        nodeData: node.data,
      });
      return;
    }
    const fixedColWidth = (colsWidth || [])
      .slice(resizingColCellIndex, this.resizingColIndex!)
      .reduce((sum, colWidth) => sum + colWidth, 0);
    const validLeftLimit =
      resizingColCellRectLeft + fixedColWidth * scale + MIN_COL_WIDTH * scale;
    const totalWidth = (colsWidth || []).reduce(
      (sum, colWidth) => sum + colWidth,
      0,
    );
    let validRightLimit = Infinity;
    if (
      this.resizingColNextCell &&
      totalWidth <= this.getContainerWidth()! - 2
    ) {
      const resizingColNextCellRect = getBoundingRelativeRect(
        this.resizingColNextCell,
        zoomContainer,
      );
      const resizingColNextCellRectLeft = resizingColNextCellRect.left;
      const resizingColNextCellIndex =
        resizingColCellIndex +
        parseInt(this.resizingColCell.getAttribute('colspan')!, 10);
      validRightLimit =
        resizingColNextCellRectLeft +
        (colsWidth[resizingColNextCellIndex] - MIN_COL_WIDTH) * scale;
    }
    let validClientX = Math.max(validLeftLimit, relativeEvent.relativeX);
    validClientX = Math.min(validRightLimit, validClientX);
    colResizer.style.left = `${Math.round(
      (validClientX - realTableWrapperRect.left) / scale,
    )}px`;
  };

  handleColResizeEnd = () => {
    const { scale } = this.props;
    const colResizer = ReactDOM.findDOMNode(
      this.colResizerRef.current,
    ) as HTMLElement;
    if (!colResizer) {
      return;
    }

    const delta =
      parseInt(colResizer.style.left, 10) -
      Math.round(this.startTableX! / scale);
    if (!isNaN(delta)) {
      this.handleColResize(this.resizingColIndex, delta);
    }

    document.removeEventListener('mousemove', this.handleColResizing);
    document.removeEventListener('mouseup', this.handleColResizeEnd);

    colResizer.style.display = 'none';
    colResizer.style.left = '';

    this.resizingColIndex = null;
    this.resizingColCell = null;
    this.resizingColNextCell = null;
    this.startTableX = null;
  };

  handleColResize = (resizeColIndex, delta) => {
    const { controller, node } = this.props;
    controller.run(
      'onAction',
      resizeTableColWidth({
        node,
        colIndex: resizeColIndex,
        delta,
      }),
    );

    this.updateShadowState();
  };

  handleRowResizeStart = (event) => {
    const { scale, zoomContainer, isSelected } = this.props;
    const relativeEvent = getRelativeMouseEvent(event, zoomContainer);
    event.preventDefault();

    const targetCell = event.target.closest('td');
    if (!targetCell) {
      return;
    }
    const resizingRowKey = getRowKey(targetCell, this.rowResizeable);
    if (!resizingRowKey) {
      return;
    }
    this.resizingRowKey = resizingRowKey;
    this.resizingRow =
      this.rowResizeable === 'top'
        ? targetCell.parentElement.previousElementSibling
        : targetCell.parentElement;

    const realTableWrapper = ReactDOM.findDOMNode(this.realTableWrapperRef.current) as HTMLElement;
    const realTableWrapperRect = getBoundingRelativeRect(
      realTableWrapper,
      zoomContainer,
    );
    const table = ReactDOM.findDOMNode(this.tableRef.current) as HTMLElement;
    const tableRect = getBoundingRelativeRect(table, zoomContainer);

    this.startTableY = relativeEvent.relativeY - realTableWrapperRect.top;

    const rowResizer = ReactDOM.findDOMNode(
      this.rowResizerRef.current,
    ) as HTMLElement;
    rowResizer.style.display = 'block';
    rowResizer.style.marginTop = '-1px';
    const tableWidth = Math.min(tableRect.width, realTableWrapperRect.width);
    rowResizer.style.width = `${Math.round(
      (tableWidth + (isSelected ? TOOLBAR_ITEM_SIZE : 0)) / scale,
    )}px`;

    rowResizer.style.top = `${Math.round(this.startTableY / scale)}px`;

    document.addEventListener('mousemove', this.handleRowResizing);
    document.addEventListener('mouseup', this.handleRowResizeEnd);
  };

  handleRowResizing = (event) => {
    const { scale, zoomContainer } = this.props;
    const relativeEvent = getRelativeMouseEvent(event, zoomContainer);
    event.preventDefault();

    const realTableWrapper = ReactDOM.findDOMNode(this.realTableWrapperRef.current) as HTMLElement;
    const rowResizer = ReactDOM.findDOMNode(
      this.rowResizerRef.current,
    ) as HTMLElement;

    // Fix: 
    if (!realTableWrapper || !rowResizer || !this.resizingRow) return;

    const realTableWrapperRect = getBoundingRelativeRect(
      realTableWrapper,
      zoomContainer,
    );
    const resizingColRectTop = getBoundingRelativeRect(
      this.resizingRow,
      zoomContainer,
    ).top;
    const validTopLimit = resizingColRectTop + MIN_ROW_HEIGHT * scale;
    const validClientY = Math.max(validTopLimit, relativeEvent.relativeY);
    rowResizer.style.top = `${Math.round(
      (validClientY - realTableWrapperRect.top) / scale,
    )}px`;
  };

  handleRowResizeEnd = () => {
    const { scale } = this.props;
    const rowResizer = ReactDOM.findDOMNode(
      this.rowResizerRef.current,
    ) as HTMLElement;
    if (!rowResizer) {
      return;
    }

    const delta =
      parseInt(rowResizer.style.top, 10) - Math.round(this.startTableY! / scale);
    if (!isNaN(delta)) {
      this.handleRowResize(this.resizingRowKey, delta);
    }

    document.removeEventListener('mousemove', this.handleRowResizing);
    document.removeEventListener('mouseup', this.handleRowResizeEnd);

    rowResizer.style.display = 'none';
    rowResizer.style.top = '';

    this.resizingRowKey = null;
    this.resizingRow = null;
    this.startTableY = null;
  };

  handleRowResize = (resizingRowKey, delta) => {
    const { controller } = this.props;
    const tableRow = controller.value.document.getNode(resizingRowKey);
    //  row
    if (!tableRow) return;
    const originalHeight = controller.query('getRowsHeight', {
      node: tableRow,
    });
    controller.run(
      'onAction',
      setTableRowHeight(tableRow, originalHeight + delta),
    );
  };

  handleSelectRow = (
    selectedRowRange: ISelectedIndexRange,
    shouldHideContextMenu = true,
  ) => {
    const { controller, node } = this.props;
    // selectionspi
    lastActiveTableKey = node.key;
    const { start, end } = selectedRowRange;
    controller.run(
      'onAction',
      selectTableRows(node, start, end),
    );
    if (shouldHideContextMenu) {
      this.closeContextMenu();
    }
  };

  handleSelectCol = (
    selectedColRange: ISelectedIndexRange,
    shouldHideContextMenu = true,
  ) => {
    const { controller, node } = this.props;
    // selectionspi
    lastActiveTableKey = node.key;
    const { start, end } = selectedColRange;
    controller.run(
      'onAction',
      selectTableCols(node, start, end),
    );
    if (shouldHideContextMenu) {
      this.closeContextMenu();
    }
  };

  handleSelectCorner = () => {
    const { controller, node } = this.props;
    // selectionspi
    lastActiveTableKey = node.key;
    controller.run('onAction', selectTable(node));
    this.closeContextMenu();
  };

  updateShadowStyleCallback = (visible: boolean) => {
    if (!visible) return;

    const { scale, zoomContainer, node: dataTable } = this.props;
    visibleUtil.unregisterVisibleChange(dataTable.key, this.updateShadowStyleCallback);
    const table = ReactDOM.findDOMNode(this.tableRef.current) as HTMLElement;
    if (!table) {
      return;
    }
    const tableRect = getBoundingRelativeRect(table, zoomContainer);

    const tableLeftShadow = ReactDOM.findDOMNode(
      this.tableLeftShadowRef.current,
    ) as HTMLElement;
    const tableRightShadow = ReactDOM.findDOMNode(
      this.tableRightShadowRef.current,
    ) as HTMLElement;

    const height = `${Math.round(tableRect.height / scale)}px`;

    if (tableLeftShadow) {
      tableLeftShadow.style.height = height;
    }
    if (tableRightShadow) {
      tableRightShadow.style.height = height;
    }
  };

  updateShadowStyle = () => {
    const { node } = this.props;
    if (visibleUtil.getVisible(node.key)) {
      this.updateShadowStyleCallback(true);
    } else {
      visibleUtil.unregisterVisibleChange(node.key, this.updateShadowStyleCallback);
      visibleUtil.registerVisibleChange(node.key, this.updateShadowStyleCallback);
    }
  };

  updateScrollLeft = () => {
    const { node } = this.props;
    const { percentOfScrollLeft } = node.data || {};
    const scrollContainer = this.realTableWrapperRef.current;
    if (!scrollContainer || !this.tableRef.current) {
      return;
    }
    if (typeof percentOfScrollLeft === 'number') {
      const containerWidth = scrollContainer.offsetWidth;
      const tableWidth = this.tableRef.current.offsetWidth;
      const totalScroll = tableWidth - containerWidth;
      scrollContainer.scrollTo({
        left: percentOfScrollLeft * totalScroll,
        behavior: 'smooth',
      });
    }
  };

  updateTableResizeController = () => {
    const { controller, scale, zoomContainer } = this.props;
    if (!this.isSelected() || !zoomContainer) {
      return;
    }
    const tableDOM = ReactDOM.findDOMNode(this.tableRef.current) as HTMLElement;
    const realTableWrapper = ReactDOM.findDOMNode(this.realTableWrapperRef.current) as HTMLElement;
    const tableResizeController = ReactDOM.findDOMNode(
      this.tableResizeControllerRef.current,
    ) as HTMLElement;
    if (!tableResizeController || !tableDOM || !realTableWrapper) {
      return;
    }
    const tableRect = getBoundingRelativeRect(tableDOM, zoomContainer);
    const realTableWrapperRect = getBoundingRelativeRect(
      realTableWrapper,
      zoomContainer,
    );
    if (!controller.query('isTableSupportResize')) {
      tableResizeController.style.display = 'none';
      return;
    }
    // 
    if (tableRect.right <= realTableWrapperRect.right + 1) {
      tableResizeController.style.display = '';
      tableResizeController.style.left =
        `${Math.round((tableRect.right - realTableWrapperRect.left) / scale)}px`;
      tableResizeController.style.top =
        `${Math.round((tableRect.bottom - realTableWrapperRect.top) / scale)}px`;
    } else {
      tableResizeController.style.display = 'none';
    }
  };

  updateOnRender = throttle(() => {
    this.updateTableResizeController();
    this.updateShadowStyle();
  });

  handleTableToolbarContextMenu = (event: React.MouseEvent<HTMLElement>) => {
    event.preventDefault();
    const { controller } = this.props;
    controller.run('onContextMenu', event);
  };

  handleMenuEvent = (type) => {
    const { controller, node } = this.props;
    let selection = this.getTableSelection();
    if (!selection) {
      selection = createTableSelectionForFocusedCell(controller.value, node);
    }
    if (type === 'delete-table') {
      controller.run('onAction', deleteTable(node));
    } else if (type === 'table-width-fit-container') {
      controller.run('onAction', resizeTableWidthFitContainer(node));
    }
  };

  updateShadowState = () => {
    // DOM  DOM 
    if (this.updateShadowRafId !== null) {
      window.cancelAnimationFrame(this.updateShadowRafId);
    }
    this.updateShadowRafId = window.requestAnimationFrame(() => {
      fastdom.measure(() => {
        const table = ReactDOM.findDOMNode(this.tableRef.current) as HTMLElement;
        const realTableWrapper = ReactDOM.findDOMNode(
          this.realTableWrapperRef.current,
        ) as HTMLElement;
        if (!table || !realTableWrapper) {
          return;
        }
        const { zoomContainer } = this.props;

        const tableRect = getBoundingRelativeRect(table, zoomContainer);

        const realTableWrapperRect = getBoundingRelativeRect(
          realTableWrapper,
          zoomContainer,
        );
        const shouldShowTableLeftShadow = realTableWrapper.scrollLeft > 0;
        const scrollableContentWidth = tableRect.width;
        const shouldShowTableRightShadow =
          scrollableContentWidth - realTableWrapper.scrollLeft >
          realTableWrapperRect.width;
        const {
          shouldShowTableLeftShadow: prevLeft,
          shouldShowTableRightShadow: prevRight,
        } = this.state;
        if (
          prevLeft !== shouldShowTableLeftShadow ||
          prevRight !== shouldShowTableRightShadow
        ) {
          this.setState({ shouldShowTableLeftShadow, shouldShowTableRightShadow });
        }
      });
    });
  };

  handleTableScroll = throttle(() => {
    const { controller, node, tableConfig } = this.props;
    const table = ReactDOM.findDOMNode(this.tableRef.current);
    if (!table || !this.realTableWrapperRef.current) {
      return;
    }
    controller.run('onAction', scrollTable(this.realTableWrapperRef.current));
    if (tableConfig?.onScroll) {
      tableConfig.onScroll(this.realTableWrapperRef.current, node);
    }
    this.updateShadowState();
  });

  getTableSelection = () => {
    const { controller, node } = this.props;
    const tblSelections = controller.query(
      'viewTableSelections',
    );
    if (!tblSelections) {
      return null;
    }
    const curTableSelection = tblSelections.find(
      (ts) => ts.key === node.key,
    );
    if (!curTableSelection) {
      return null;
    }
    return curTableSelection;
  };

  getTableLeftMaxBorder = (table: MoTable) => {
    return table.nodes.reduce((acc, row: Block) => {
      if (!row.nodes) {
        return acc;
      }
      const firstCell = row.nodes[0] as Block;
      if (
        firstCell &&
        firstCell.data &&
        !firstCell.data.hidden &&
        firstCell.data.bdr &&
        firstCell.data.bdr.left &&
        firstCell.data.bdr.left.sz > acc
      ) {
        return firstCell.data.bdr.left.sz;
      }
      return acc;
    }, 1);
  };

  getAllColsNodes = (colsNodes: HTMLDivElement[]) => {
    this.colsNodes = colsNodes;
  };

  handleSetIsHighlightSelection = (isHighlight: boolean) => {
    this.setState({
      isSelectionHighlight: isHighlight,
    });
  };

  handleSetHoverSelection = (sel: ITableSelection | null) => {
    this.setState({
      hoverSelection: sel,
    });
  };

  isShowCornerToolbar = () => {
    const { tableConfig } = this.props;
    return !tableConfig?.hideCornerButton?.();
  };

  handleMouseEnterTable = () => {
    //  mouseenter  mouseleave
    //  isHoverTable
    if (this.isResizingTable) return;

    this.setState({
      isHoverTable: true,
    });
  };

  handleMouseLeaveTable = () => {
    if (this.isResizingTable) return;

    this.setState({
      isHoverTable: false,
    });
  };

  setWholeTableHoverSelection = () => {
    const cols = this.props.node.data?.colsWidth?.length;
    const rows = this.props.node?.nodes.length;
    if (!cols || !rows) {
      this.handleSetHoverSelection(null);
      return;
    }
    this.handleSetHoverSelection({
      key: this.props.node.key,
      endColIndex: cols - 1,
      endRowIndex: rows - 1,
      startColIndex: 0,
      startRowIndex: 0,
    });
  };

  removeWholeTableHoverSelection = () => {
    this.handleSetHoverSelection(null);
  };


  handleMouseEnterCornerToolbar = () => {
    this.setWholeTableHoverSelection();
    this.setState({
      isHoverCornerToolbar: true,
    });
  };

  handleMouseLeaveCornerToolbar = () => {
    this.removeWholeTableHoverSelection();
    this.setState({
      isHoverCornerToolbar: false,
    });
  };

  render() {
    const {
      controller,
      node,
      children,
      locale,
      scale = 1,
      zoomContainer,
      scrollableContainer,
      tableConfig = {},
      draggableConfig,
      visible,
    } = this.props;

    const {
      hideDeleteButton = false,
      hideWidthFitButton = false,
      enableAutofitWidth,
      enableHeader,
      setTableHeaderStyle,
    } = tableConfig;

    const {
      shouldShowTableLeftShadow,
      shouldShowTableRightShadow,
      isSelectionHighlight,
      hoverSelection,
      isHoverCornerToolbar,
    } = this.state;

    const leftOffset = Math.floor(this.getTableLeftMaxBorder(node) / 2);

    const isSelected = this.isSelected();
    const isSelectWholeTable = controller.query('isSelectWholeTable', {
      node,
    });

    const isHideDeleteButton = typeof hideDeleteButton === 'function'
      ? hideDeleteButton() : hideDeleteButton;

    const style: React.CSSProperties = {};

    if (visible === false) {
      style.display = 'none';
    }

    return (
      <Wrapper
        ref={this.wrapperRef}
        onClick={() => {
          // onClick onMouseDown
          lastActiveTableKey = node.key;
        }}
        onMouseDown={this.handleMouseDown}
        onMouseEnter={this.handleMouseEnterTable}
        onMouseLeave={this.handleMouseLeaveTable}
        style={style}
        data-type="table"
      >
        <TableIsSelectedContext.Provider value={isSelected}>
          <TableScrollContainer
            className="real-table-wrapper"
            table={node}
            controller={controller}
            ref={this.realTableWrapperRef}
            onMouseEnter={this.handleMouseEnter}
            onMouseMove={this.handleMouseMove}
            onScroll={this.handleTableScroll}
            disableHorizontalScrollbar={tableConfig?.disableHorizontalScrollbar}
            // note: borderoverflow auto
            style={{
              marginLeft: -leftOffset,
              paddingLeft: leftOffset,
              //  paddingTopmarginTop  overflow hidden
              paddingTop: REALTABLE_PADDING.top / scale,
              marginTop: -REALTABLE_PADDING.top / scale,
              paddingBottom: REALTABLE_PADDING.bottom, // 1pxbottom
            }}
          >
            <TableColToolbar
              locale={locale}
              controller={controller}
              table={node}
              tableRef={this.tableRef}
              positionedAncestorRef={this.realTableWrapperRef}
              scrollContainer={this.realTableWrapperRef}
              colIndicatorRef={this.colIndicatorRef}
              isTableSelected={isSelected}
              onContextMenu={this.handleTableToolbarContextMenu}
              onSelect={this.handleSelectCol}
              onColResize={this.handleColResize}
              onGetAllColsNodes={this.getAllColsNodes}
              zoomContainer={zoomContainer}
              getLastActiveTableKey={getLastActiveTableKey}
              scale={scale}
              setIsHighlightSelection={this.handleSetIsHighlightSelection}
              hoverSelection={hoverSelection}
              setHoverSelection={this.handleSetHoverSelection}
              isHideDeleteButton={isHideDeleteButton}
              isHoverCornerToolbar={isHoverCornerToolbar}
            />
            <TableWithSelection
              ref={this.tableRef}
              node={node}
              controller={controller}
              left={-leftOffset}
              scale={scale}
              tableSelection={this.getTableSelection()}
              getTableSelection={this.getTableSelection}
              hoverSelection={hoverSelection}
              isSelectionHighlight={isSelectionHighlight}
            >{children()}
            </TableWithSelection>
          </TableScrollContainer>
          <TableResizeController
            visible={isSelected}
            onMouseDown={this.handleTableResizeStart}
            ref={{
              // @ts-ignore
              tableResizeControllerRef: this.tableResizeControllerRef,
              tableResizeControlLinesRef: this.tableResizeControlLinesRef,
            }}
          />
          <ColResizer
            ref={this.colResizerRef}
          />
          <RowResizer
            ref={this.rowResizerRef}
            style={{ left: isSelected ? -TOOLBAR_ITEM_SIZE / scale! : 0 }}
          />
          <ColIndicator
            ref={this.colIndicatorRef}
            isTableSelected={isSelected}
            data-testid="table-col-indicator"
            scale={scale}
          />
          <RowIndicator
            ref={this.rowIndicatorRef}
            isTableSelected={isSelected}
            data-testid="table-row-indicator"
          />
          <TableLeftSideShadow
            ref={this.tableLeftShadowRef}
            scale={scale}
            style={{
              display: shouldShowTableLeftShadow ? 'block' : 'none',
            }}
          />
          <TableRightSideShadow
            ref={this.tableRightShadowRef}
            scale={scale}
            style={{
              display: shouldShowTableRightShadow ? 'block' : 'none',
            }}
          />
          <TableRowToolbar
            locale={locale}
            isSelected={isSelected}
            controller={controller}
            table={node}
            tableRef={this.tableRef}
            offsetParentRef={this.wrapperRef}
            realTableWrapperRef={this.realTableWrapperRef}
            rowIndicatorRef={this.rowIndicatorRef}
            isSelectWholeTable={isSelectWholeTable}
            onContextMenu={this.handleTableToolbarContextMenu}
            onSelect={this.handleSelectRow}
            onRowResize={this.handleRowResize}
            zoomContainer={zoomContainer}
            getLastActiveTableKey={getLastActiveTableKey}
            scale={scale}
            setIsHighlightSelection={this.handleSetIsHighlightSelection}
            setHoverSelection={this.handleSetHoverSelection}
            hoverSelection={hoverSelection}
            scrollableContainer={scrollableContainer}
            isHideDeleteButton={isHideDeleteButton}
            isHoverCornerToolbar={isHoverCornerToolbar}
          />
          {this.isShowCornerToolbar() && (
            <TableCornerToolbar
              locale={locale}
              visible={this.isSelected() || this.state.isHoverTable}
              controller={controller}
              realTableWrapperRef={this.realTableWrapperRef}
              table={node}
              isSelectWholeTable={!!isSelectWholeTable}
              onSelect={this.handleSelectCorner}
              onMouseEnter={this.handleMouseEnterCornerToolbar}
              onMouseLeave={this.handleMouseLeaveCornerToolbar}
              onToolbarEvent={this.handleMenuEvent}
              zoomContainer={zoomContainer}
              getLastActiveTableKey={getLastActiveTableKey}
              scale={scale!}
              setIsHighlightSelection={this.handleSetIsHighlightSelection}
              hideWidthFitButton={hideWidthFitButton}
              draggableConfig={draggableConfig}
              enableAutofitWidth={enableAutofitWidth}
              enableHeader={enableHeader}
              setTableHeaderStyle={setTableHeaderStyle}
            />
          )}
          <CornerButton
            scale={scale!}
            isSelected={isSelected}
            onClick={this.handleSelectCorner}
            active={isSelectWholeTable || isHoverCornerToolbar}
            onMouseEnter={this.handleMouseEnterCornerToolbar}
            onMouseLeave={this.handleMouseLeaveCornerToolbar}
            hoverSelection={!!hoverSelection}
          />
        </TableIsSelectedContext.Provider>
      </Wrapper>
    );
  }
}

const Container: React.FC<Omit<TableProps, 'colsWidth' | 'scale' | 'zoomContainer'>> = (props) => {
  const scrollableContainer = useScrollableContainer();
  const zoomContainer = useZoomContainer() || window.document.body;
  const scale = useZoom();
  const { controller, node, visible } = props;

  const [pendingSelectedWhole, setPendingSelectedWhole] = React.useState<boolean>(false);

  // pendingEnableselectionData$table
  // 500ms
  // pendingEnablefalseuseSelectionStatus
  const [realSelected, setRealSelected] = React.useState<boolean>(false);
  const [, forceUpdate] = React.useReducer((c) => !c, false);

  React.useEffect(() => {
    const newTable = controller.value.document.getNode(node.key) as MoTable;
    if (!newTable) return;
    setRealSelected(getTableIsSelected(newTable, controller));
  }, [node, controller.value.selection, controller]);

  const shouldUpdateTable = React.useCallback(({ isPendingSelected, isSelectWholeTable }) => {
    setRealSelected(isPendingSelected);
    setPendingSelectedWhole(isSelectWholeTable);
    if (controller.hasHots()) {
      forceUpdate();
    }
  }, [controller, node]);

  useSelectionStatus(controller, node, shouldUpdateTable, []);

  React.useEffect(() => {
    controller.run('onAction', adjustColsWidth(node));
  });

  const memoProps = React.useMemo(() => {
    return { ...props, isSelected: realSelected, isSelectWholeTable: pendingSelectedWhole };
  }, [props, realSelected, pendingSelectedWhole]);

  return (
    <ContextContainer isSelected={realSelected}>
      <PixelColsWidthContext.Consumer>
        {([pixelColsWidth]) => (<Table
          {...memoProps}
          scrollableContainer={scrollableContainer}
          zoomContainer={zoomContainer}
          scale={scale || 1}
          colsWidth={pixelColsWidth}
          visible={visible}
        />)}
      </PixelColsWidthContext.Consumer>
    </ContextContainer>
  );
};

export default (props: Omit<TableProps, 'colsWidth' | 'scale' | 'zoomContainer'>) => {
  const { controller, node, tableConfig = {} } = props;
  const { enableHeaderSticky } = tableConfig;

  return (
    <TableContextContainer
      controller={controller}
      table={node}
      enableHeaderSticky={enableHeaderSticky}
    >
      <Container {...props} />
    </TableContextContainer>
  );
};
